<template>
  <v-app>
    <bm-right-sidebar></bm-right-sidebar>
    <v-main>
      <div class="table-styling">
      <v-breadcrumbs class="breadcrumbs">..</v-breadcrumbs>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="networks"
        class="elevation-1"
        item-key="network_uuid"
        show-select
        :footer-props="tableProps.FOOTER_PROPS"
        :itemsPerPage="tableProps.ITEMS_PER_PAGE"
        :search="search"
      >
        <template v-slot:top >
          <v-toolbar flat>
            <v-toolbar-title>NETWORKS</v-toolbar-title>
            <v-divider class="mx-4" inset vertical></v-divider>
            <div class="text-left pa-2">
              <v-btn @click="onClickRefresh" x-small color="primary" class="white--text" > Refresh <v-icon x-small right dark> mdi-refresh </v-icon> </v-btn>&nbsp;
              <v-btn @click="dialog = true" x-small color="success" class="white--text" > Create <v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;
              <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text"> Delete <v-icon x-small right dark > mdi-delete </v-icon> </v-btn>&nbsp;
              <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                </template>
                <v-list>
                  <v-list-item link>
                    <v-list-item-title @click="clickDownload(networks, 'json')">Json</v-list-item-title>
                  </v-list-item>
                  <v-list-item link>
                    <v-list-item-title @click="clickDownload(networks, 'excel')">Excel</v-list-item-title>
                  </v-list-item>
                  <v-list-item link>
                    <v-list-item-title @click="clickDownload(networks,'csv')">CSV</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>&nbsp;
              <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on" >Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                </template>
                <v-list>
                  <v-list-item link>
                    <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                  </v-list-item>
                  <v-list-item link>
                    <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>&nbsp;
              <v-btn @click="clickJsonExport(selected)" :disabled="selected.length !== 1" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon></v-btn>&nbsp;
              <v-btn @click="onClickOpenDevicesView" x-small color="blue-grey" class="white--text" > View All Devices<v-icon x-small right dark> mdi-collage </v-icon> </v-btn>&nbsp;
              <v-btn @click="onClickOpenPointsViewOfAllNetworks" x-small color="deep-purple" class="white--text" > View All Points<v-icon x-small right dark> mdi-view-headline </v-icon> </v-btn>&nbsp;
            </div>
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
          </v-toolbar>
        </template>

        <template v-slot:item.network_name="{ item }">
          <a @click="onClickOpen(item)"> {{ item.network_name }} </a>
        </template>

        <template v-slot:item.network_enable="{ item }" >
          <v-checkbox v-model="item.network_enable" @click="updateNetwork(item)"></v-checkbox>
        </template>

        <template v-slot:item.actions="{ item }" >
          <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>
          <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>
          <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
          <v-tooltip bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-icon v-bind="attrs" v-on="on" small @click="onClickOpenPointsView(item)">mdi-view-headline</v-icon>
            </template>
            <span>View all points</span>
          </v-tooltip>
        </template>

        <template v-slot:header.network_enable="{ item }" >
          <div class="d-flex">
            <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
            <v-icon x-small @click="openMassEditDialog('network_enable')" color="primary">mdi-pencil</v-icon>
          </div>
        </template>

        <template v-slot:no-data> No data </template>
      </v-data-table>
      </div>
    </v-main>

    <!-- create/edit dialog -->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field @input="changeName" v-model="editedItem.network_name" single-line label="Network name" required :rules="[() => !isExistingvalue(editedItem.network_name, 'network_name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.network_enable" label="Enable" required></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.network_ip" label="IP"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.network_port" label="Port" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.network_mask" label="Mask" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="editedItem.network_device_object_id" label="Device Id" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field v-model="editedItem.network_device_name" label="Device name"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport" >
      <v-toolbar flat dark >
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport" >
            <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1" > Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2" > Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport" ></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="headers"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="network_uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top >
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)" v-if="!isClickSaveImport"> mdi-delete </v-icon>
                  <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.network_uuid, 'network_uuid')">mdi-alert-octagram-outline</v-icon>
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.network_uuid, 'network_uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createNetwork(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.network_uuid, 'network_uuid')">mdi-cached</v-icon>
                </template>
                <template v-slot:item.network_name="{ item }" >
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.network_uuid, 'network_uuid')" v-model="item.network_name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.network_name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.network_name, 'network_name') || 'Name Existed', rules.required]" ></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey, conventionNaming } from "../../utils/utils";
import axios from "../../axios";
import * as xlsx from "xlsx";
import _cloneDeep from 'lodash/cloneDeep';
import _forIn from 'lodash/forIn';
import MqttConsole from '../Mqtt/MqttConsole';
import BmRightSidebar from './BmRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import * as changeCase from "change-case";
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import MbAllPointViewOfAllNetworksVue from '../Modbus/MbAllPointViewOfAllNetworks.vue';

export default {
  components: {
    MqttConsole,
    BmRightSidebar,
    ConfirmDialog,
    ImportFromExcelDialog,
  },
  data: () => ({
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    headers: [
      { text: "Actions", value: "actions", sortable: false },
      { text: "Name", value: "network_name", align: "start" },
      { text: "Enable", value: "network_enable", sortable: false },
      { text: 'Network Mask', value: 'network_mask', sortable: false },
      { text: 'Network IP', value: 'network_ip', sortable: false },
      { text: 'Network Port', value: 'network_port', sortable: false },
      { text: "Device Id", value: "network_device_object_id", align: "start", sortable: false },
      { text: "Device Name", value: "network_device_name", align: "start", sortable: false },
      { text: 'UUID', value: 'network_uuid', sortable: false },
    ],
    networks: [],
    currentItem: {},
    editedIndex: -1,
    editedItem: {
      network_name: 'Nube-Master',
      network_enable: true,
      network_mask: 24,
      network_ip: '192.168.15.10',
      network_port: 47808,
      network_device_object_id: 54321,
      network_device_name: 'nube master',
    },
    defaultItem: { 
      network_name: 'Nube-Master',
      network_enable: true,
      network_mask: 24,
      network_ip: '192.168.15.10',
      network_port: 47808,
      network_device_object_id: 54321,
      network_device_name: 'nube master',
    },
    rules: { required: (value) => !!value || "Required." },
    action: "",
    dataImport: "",
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    search:"",
    searchWizard:"",
    dialogMassEdit: false,
    editAll: {
      network_enable: false,
    },
    editField: "",
    intervalId: null,
    isClickSaveImport: false,
    tableProps: {...DATA_TABLE_PROPS},

  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Create Network/Interface" : "Edit Network/Interface";
    },
    pingData(){
      return this.$store.state.pingData;
    },
    networksData () {
      return this.$store.state.bmNetworks;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.bacnet;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$store.state.pingData':function(val){
      this.getPingData(val)
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    next();
  },
  mounted() {
    this.getNetworks();
    if(Object.keys(this.pingData).length!==0){
      this.getPingData(this.pingData);
    }
    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getNetworks();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getPingData(data){
      if(Object.keys(data).length===0){
        this.$toasted.show('Point Server is not running', {type:"error"});
        return;
      }
    },
    // EXAMPLE GETS
    getNetworks() {
      axios.get( 'bacnet_master/networks' )
        .then((data) => {
          this.networks = data.networks;
          this.$store.commit('changeBmNetworksData', this.networks);
        });
    },

    onClickOpen(item){
      this.$router.push({ path: `/bacnet-master/networks/${item.network_uuid}/devices` });
    },
    editItem(item) {
      this.editedIndex = this.networks.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateNetwork(this.editedItem);
        } else {
          this.createNetwork(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = "deleteSingle";
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
      this.selected = [];
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = "deleteSelected";
    },
    async deleteSingle() {
      this.editedIndex = this.networks.indexOf(this.currentItem);
      this.networks.splice(this.editedIndex, 1);
      await axios.delete(`bacnet_master/network/${this.currentItem.network_uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = "";
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = "";
      });
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if(this.importArray && this.importArray.length > 0){
        if(this.$refs.form.validate()){
          this.isClickSaveImport = true;
          try{
            const promises = [];
            for (const item of this.importArray) {
              if(!item.network_uuid){
                promises.push(this.createNetwork(item));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    async clickImportJson(){
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
    },
    clickContinue(){
      if(!isJsonString(this.dataImport) ){
        return this.$toasted.show("incorrect data type!", { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(nw=>{
        delete nw.network_uuid;
        return nw;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      let networks = _cloneDeep(this.networks);
      if(this.editedIndex !== -1){ //when edit
        networks.splice(this.editedIndex, 1);
      }
      return networks.find(n => n[fieldName] == value) != null;
    },
    convertExcelToJson(file){
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = (event)=>{
        let data = event.target.result;
        let workbook = xlsx.read(data,{type:"binary"});
        workbook.SheetNames.forEach(sheet => {
            let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
            const items = rowObject.map((i) => {
              for(let key in i){
                const newKey = key.trimRight("");
                const value = i[key];
                delete i[key];
                i = {...i, [newKey]: value}
              }
              i.devices = this.getDevicesArray(i);

              return i;
            });
            this.dataImport = JSON.stringify(items);
            this.dialogImportExcel = false;
            this.dialogImport = true;
            this.e1 = 1;
        });
      }
    },
    getDevicesArray(network){
      let devices = [];
      let deviceIndexs = [];
      let deviceKeys = [];

      Object.keys(network).forEach(key =>{ //get deviceIndexs and deviceKeys
        let keys = key.split('.');
        let deviceKey = "";
        let deviceIndex = null;
        if(keys.length > 1){
          deviceKey = keys[1];
          if(!deviceKeys.includes(deviceKey)){
            deviceKeys.push(deviceKey);
          }
          deviceIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((deviceIndex || deviceIndex === 0) && !deviceIndexs.includes(deviceIndex)){
            deviceIndexs.push(deviceIndex);
          }
        }
      })
      //get device
      deviceIndexs.forEach(i=>{
        let device = {};
        deviceKeys.forEach(dk=>{
          device = {...device, [dk]: network[`device[${i}].${dk}`]};
        });
        //get points of device
        Object.keys(network).forEach(key =>{
          let keys = key.split('.');
          if(keys[0] == `device[${i}]` && keys.length > 2){
            device = {...device, [`${keys[1]}.${keys[2]}`]: network[key]};
            device.points = this.getPointsArray(device);
          }
        });
        devices.push(device);
      })
      return devices;
    },
    getPointsArray(device){
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key =>{
        let keys = key.split('.');
        let pointKey = "";
        let pointIndex = null;
        if(keys.length > 1){
          pointKey = keys[1];
          if(!pointKeys.includes(pointKey)){
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split("[")[1].slice(0, -1));
          if((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)){
            indexs.push(pointIndex);
          }
        }
      })
      indexs.forEach(i=>{
        let point = {};
        pointKeys.forEach(pk=>{
          point = {...point, [pk]: device[`point[${i}].${pk}`]}
        });
        points.push(point);
      })
      return points;
    },
    async clickJsonExport(){
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type){
      const data = this.convertExportData(array);
      if(type === "csv"){
          return exportCSV("networks", data);
      } if(type === "excel"){
          return exportExcel("networks", data);
      } if(type === "json"){
          return exportJSON("networks", JSON.stringify(this.networks));
      }
    },
    convertExportData(array){
      const data = _cloneDeep(array).map(network=>{
        if(network.devices){
          network.devices.forEach((device, indexD)=>{
            //devices
            _forIn(device, (value, key) => {
              if(key != 'points'){
                const keyName = `device[${indexD}].${key}`;
                network = {...network, [keyName] : value}
              }else{
                //points
                device.points.forEach((point, indexP)=>{
                  _forIn(point, (point_value, point_key) => {
                    const pointKey = `device[${indexD}].point[${indexP}].${point_key}`;
                    network = {...network, [pointKey] : point_value}
                  });
                });
                delete device.points
              }
            });
          });
        }
        delete network.devices
        return network;
      });
      return data;
    },
    updateNetwork(data) {
      axios.patch(`bacnet_master/network/${data.network_uuid}`, data )
        .then((response) => {
          const index = this.networks.findIndex(n=> n.network_uuid == response.network_uuid);
          Object.assign(this.networks[index], response);
          this.updateBmNetworksData(data);
          this.close();
          this.$toasted.show("update success");
        });
    },
    async createNetwork(data) {
      await axios.put(`bacnet_master/network`, data )
        .then((response) => {
          this.networks.push(response);
          if (this.importArray && this.importArray.length > 0) {
            const index = this.importArray.findIndex(n => n.network_name == response.network_name);
            if(index > -1){
              Object.assign(this.importArray[index], response);
            }
          }
          if(data.devices && data.devices.length > 0){
            for (const device of data.devices) {
              device.network_uuid = response.network_uuid;
              this.createDevice(device);
            }
          }
          this.updateBmNetworksData(data);
          this.close();
          this.$toasted.show("create success");
        });
    },
    updateBmNetworksData(newNetwork){
      const indexNetWork = this.networksData.findIndex(nw=>nw.network_uuid == newNetwork.network_uuid);
      if(indexNetWork > -1){
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeBmNetworksData', this.networksData);
      }
    },
    createDevice(data) {
      axios.put( `bacnet_master/device`, data )
        .then((response) => {
          if(data.points && data.points.length > 0){
            for (const point of data.points) {
              point.device_uuid = response.device_uuid;
              this.createPoint(point);
            }
          }
        });
    },
    createPoint(data) {
      axios.put( `bacnet_master/point`, data ).then((response) => {});
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateNetwork(item);
      }
      this.reset();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    reset(){
      this.editAll = {
        network_enable: false,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit() { },
    closeEdit() { },
    onClickOpenPointsView(network){
      this.$router.push({ path:`/bacnet-master/networks/${network.network_uuid}/points-view` });
    },
    onClickRefresh(){
      this.getNetworks();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onClickOpenDevicesView(){
      this.$router.push({ path:`/bacnet-master/networks/devices-view` });
    },
    onClickOpenPointsViewOfAllNetworks(){
      this.$router.push({ path:`/bacnet-master/networks/all-networks-points-view` });
    },
    changeName(val){
      this.editedItem.network_name = conventionNaming(val, this.setting.caseType);
    },
  },
};
</script>
